import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { TransferList } from "../../cmps/TransferList/TransferList";
import { TransferFund } from "../../cmps/TransferFund/TransferFund";
import { getContactById } from "../../store/actions/contactActions";
import { transferService } from "../../services/transferService";
import {
  setLoggedInUser,
  transferCoins,
} from "../../store/actions/userActions";
import altContactImg from "../../assets/icons/contact.png";
import { TransfersTable } from "../../cmps/TransfersTable/TransfersTable";

// import "./ContactDetailsPage.scss";

export const ContactDetailsPage = (props) => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const contact = useSelector((state) => state.contactReducer.currContact);
  const [userContactTransfers, setUserContactTransfers] = useState(null);
  const dispatch = useDispatch();

  if (!loggedUser) dispatch(setLoggedInUser());

  useEffect(() => {
    dispatch(getContactById(props.match.params.id));
  }, [dispatch, props.match.params.id]);

  useEffect(() => {
    const getTransfersByContact = async () => {
      const transfers = await transferService.getTransfersByContactId(
        contact._id
      );
      if (transfers.length) {
        setUserContactTransfers(transfers);
      }
    };
    if (contact) getTransfersByContact();
  }, [contact]);

  const onGoContactPage = () => {
    props.history.push("/contact");
  };

  const onEditContact = () => {
    props.history.push(`/contact/edit/${contact._id}`);
  };

  const onTransferCoins = (transferAmount, ev) => {
    ev.preventDefault();
    if (loggedUser.coins < transferAmount) return alert("No enough money");
    dispatch(transferCoins(contact.contactEmail, transferAmount));
  };

  if (!contact) return <div>Contact not found...</div>;
  return (
    <div className="contact-details-page">
      <div className="action-buttons">
        <button onClick={onGoContactPage}>Back</button>
        <button onClick={onEditContact}>Edit</button>
      </div>
      <div className="contact-details">
        {contact.contactImg ? (
          <img src={contact.contactImg} alt="" />
        ) : (
          <img src={altContactImg} alt="" />
        )}
        <p>Name: {contact.contactName}</p>
        <p>Email: {contact.contactEmail}</p>
        <p>Phone: {contact.contactPhone}</p>
      </div>
      <TransferFund contact={contact} onTransferCoins={onTransferCoins} />
      {userContactTransfers && (
        // <TransferList
        //   transfers={userContactTransfers}
        //   title="Transfers with contact"
        // />
        <TransfersTable rows={userContactTransfers} />
      )}
    </div>
  );
};
